<script>
import Modal from './Modal.vue';

export default {
  name: 'AboutMe',
  props: {
  },
  components: {
    Modal
  },
  data: function() {
    return {
      message: '',
      modalType: '',
      modalTitle: '',
      tinkSound: null,
      shakeSound: null,
      isModalVisible: false
    }
  },
  methods: {
    playSound: function(sound) {
      if (sound === 'tinkSound') {
        this.tinkSound.currentTime = 0
        this.tinkSound.play();
      } else if (sound === 'shakeSound') {
        this.shakeSound.currentTime = 0
        this.shakeSound.play();
        }
    },
    showModal(modalType, modalTitle, message) {
      this.modalType = modalType;
      this.modalTitle = modalTitle;
      this.message = message;
      this.isModalVisible = true;
      document.querySelector('body').style.overflow = 'hidden'
    },
    closeModal() {
      this.isModalVisible = false;
      document.querySelector('body').style.overflow = 'scroll'
    }
  },
  mounted: function() {
    this.tinkSound = document.querySelector('#tinkSound');
    this.shakeSound = document.querySelector('#shakeSound');
    this.tinkSound ? this.tinkSound.muted = false : undefined;
    this.shakeSound ? this.shakeSound.muted = false: undefined;
  }
}
</script>


